* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #297eff;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.search .icon {
  width: 60px;
  height: 60px;
  z-index: 999;
  cursor: pointer;
  background: #fff;
  border-radius: 60px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.search .input {
  width: 300px;
  height: 60px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  left: 60px;
}

.search .input input {
  font: inherit;
  width: 100%;
  height: 100%;
  border: 0;
  outline: 0;
  padding: 10px 0;
  position: absolute;
  top: 0;
  left: 0;
}

.search {
  width: 60px;
  height: 60px;
  background: #fff;
  border-radius: 60px;
  transition: all .5s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 0 5px #2573ef;
}

.search .icon:before {
  content: "";
  height: 15px;
  width: 15px;
  border: 3px solid #287dfc;
  border-radius: 50%;
  position: absolute;
  transform: translate(-2px, -2px);
}

.search .icon:after {
  content: "";
  width: 3px;
  height: 12px;
  background: #287dfc;
  position: absolute;
  transform: translate(8px, 8px)rotate(-45deg);
}

.search.active {
  width: 350px;
}

.clear {
  width: 15px;
  height: 15px;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}

.clear:before {
  content: "";
  width: 1px;
  height: 15px;
  background: #999;
  position: absolute;
  transform: rotate(45deg);
}

.clear:after {
  content: "";
  width: 1px;
  height: 15px;
  background: #999;
  position: absolute;
  transform: rotate(-45deg);
}

